@import '../variables';

.navigation {
  width: $navigationWidth;
  background-color: #485d76;
  box-shadow: inset -1px 0 0 0 #202a35;
  position: fixed;
  top: $cozyBarHeight;
  left: 0;
  bottom: 0px;
  @include display-flex;
  @include flex-direction(column);

  @media screen and (max-width: 767px) {
    display: none;
  }

  > nav {
    &:first-child {
      @include flex(1);
    }

    ul {
      list-style: none;
      padding: 15px;
      li {

        a {
          font-family: $fontFamily;
          font-size: 16px;
          font-weight: 300;
          letter-spacing: 1px;
          color: #dddddd;
          text-decoration: none;
          display: block;
          padding: 14px 12px;

          i {
            margin-right: 14px;
            color: #e4e4e4;
          }
        }
        &.active, &:hover {
          a {
            background-color: #334254;
            box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.5);

            i {
              color: $positive;
            }
          }
        }

      }
    }

  }
}



