@import "../../variables";

.directory-picker .directory-picker-content {

    padding: 10px;
    border: 1px solid black;
    margin: 5px;
    background-color: white;

    max-height: 350px;
    overflow: auto;

    text-align: left;

    .content {
        margin-left: 8px;
        padding-left: 8px;
        border-left: 1px dotted black;
    }

    .content li {
        padding: 0;

        &:hover {
            background-color: inherit;
        }
    }

    .name {
        cursor: pointer;
        font-size: 14px;
        color: #4A4A4A;
        font-family: HelveticaNeue;
        letter-spacing: 1px;
        line-height: 2em;
        display: block;
        padding-left: 8px;

        &:hover, &.selected {
            background-color: #E7E7E7;
        }

    }

}



